import { Injectable } from '@angular/core';
import { SharedServices } from '../../../../shared/services/services.module';

@Injectable()
export class LogoService {
  logoDetails: any = {
    src: ''
  };

  constructor(
    private services: SharedServices
  ) {
  }

  setLogoOnLoad(sid: string, formObj: any, size?: string){
    if(formObj.logo.hasLogo){
      this.logoDetails.src =  this.services.constants.apiUrl + 'v1/survey/' + sid + '/logo?size=' + (size || formObj.logo.size) + '&t=' + new Date().getTime();
    }
  }

  removeLogo(sid: string, formObj: any){
    this.services.resource.removeLogo(sid).subscribe((data) => {
      formObj.logo.hasLogo = 0;
    })
  }

  logoUploadChange(sid: string, file: any, formObj: any){
    let logoSrc = '';
    let fileExtension = ['jpeg', 'jpg', 'png', 'gif', 'bmp'];
    let ext = file.name.split('.').pop().toLowerCase();
    if(fileExtension.indexOf(ext) == -1){
      this.services.alert({
        title: '<p class="sweetalert_title">Only formats are allowed : ' + fileExtension.join(',') + '</p>',
        text: "",
        type: 'warning',
      })
      return;
    }
    let bytesInMB = file.size/(1024*1024);
    if (bytesInMB > 1) {
      this.services.alert({
        title: '<p class="sweetalert_title">File size should be less than 1 MB</p>',
        text: "",
        type: 'warning',
      })
      return;
    }
    let resData = new FormData();
    resData.append('profile', file);
    this.services.resource.addLogo(sid, resData).subscribe((data) => {
      logoSrc = window.location.protocol + '//' + window.location.hostname + ':9995/v1/survey/' + sid + '/logo?size=' + formObj.logo.size + '&t=' + new Date().getTime();
      formObj.logo.hasLogo = 1;
      this.logoDetails.src = logoSrc;
    })
  }

}



