import { Injectable } from '@angular/core';
import { QuestionTypesService } from '../../../shared/questionTemplates/question-types.service';
import { SharedServices } from '../../../shared/services/services.module';
import { EditConstantsService } from './edit.constants.service';
import { BehaviorSubject, Subject, Observable } from 'rxjs';

@Injectable()
export class EditDataService {
  sId: string;
  selectedQuesType: any = {type: ''};
  questionTypesJSON: any;
  addQuestionRelatedData: any = {
    surId: '',
    pageId: '',
    pageIndex: '',
    addQuesRefId: '',
    addQuesRefIndex: '',
    updateQuesIndex: ''
  };
  surveyDesignData: any = {
    presentAction: '', //<'survey-flow','add-question','copy-question','page-delete'>
    quesNav: 'new', //<'new','bank','del'>
    displaySidebar: true
  };
  private tipsEnable: boolean = true

  constructor(
    private questionTypesService: QuestionTypesService,
    private services: SharedServices,
    private editConstants: EditConstantsService
  ) {
    this.questionTypesJSON = this.questionTypesService.questionTypesJSON;
    this.selectedQuesType.type = Object.keys(this.questionTypesJSON)[0];
  }

  getSurveyData(sId?: string): Observable<any> {
    return this.services.form.getSurveyData(this.sId);
  }

  getLogicType(str: string){
    if (str == "compound") {
        return "Delayed Logic";
    } else if (str == "protractor") {
        return "Ticket Logic";
    } else if (str == "display") {
        return "Display Logic";
    } else if (str == "promoter") {
        return "Promoter Logic";
    } else if (str == "embed") {
        return "Embedded Logic";
    } else if (str == "skip") {
        return "Skip Logic";
    }
    return str;
  }

  setTipsEnable(tipsEnable: boolean) {
    this.tipsEnable = tipsEnable;
  }

  getTipsEnable() {
    return this.tipsEnable;
  }

}



